#app4.app4 {
  align-items: center;
  display: grid;
  justify-items: center;
  position: relative;
}

#app4 .circle {
  border: 1px solid #f23f5d;
  border-radius: 50%;
  height: 20vw;
  position: relative;
  width: 20vw;
}

#app4 .circle.active {
  animation: change 1s infinite both linear;
}

#app4 .circle .heart {
  animation: heart 2.5s cubic-bezier(0.75, 0, 0.5, 1) infinite normal;
  left: 50%;
  position: absolute;
  top: 50%;
}

@keyframes change {
  0% {
    background-color: #fde755;
    transform: scale(0.5);
  }
  100% {
    background-color: #f23f5d;
    transform: scale(0.8);
  }
}
